@import '~node_modules/modern-normalize/modern-normalize.css';

body {
    font-family: 'Poppins';
    font-weight: normal;
    background-color: #0C0B0B;
    margin: 0;
    padding: 0;
}

button {
    cursor: pointer;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    padding: 0;
}

ul, li {
    list-style-type: none;
    margin: 0;
    padding: 0;
}

img {
    display: block;
}

a {
    text-decoration: none;
    color: inherit;
}

@media screen and (min-width: 1200px) {

    body{
        width: 1200px;
        margin-left: auto;
        margin-right: auto;
    }
}